.headerContainer {
    display: flex;
    flex-direction: column;
    padding-top: 85px;
    background-color: black;
    height: 100vh;
}

.catalogContent {
    width: 100%;
    display: flex;
    height: 100%;
    justify-content: space-around;
    background-color: grey;
}

.catalogHeader h1 {
    padding-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    ;
    padding-bottom: 40px;
}

.Prod__content {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 20px;
    /* height: 100%; */
    justify-content: space-around;
    background-color: rgb(153, 153, 153);
}

.prodItem {
    /* width: 22%; */
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center
}

.country {
    color: antiquewhite;
    padding-top: 20px;
}

a {
    cursor: pointer;
}

.prodItem:hover a {
    color: red;
}

.catalogImg {
    transition-duration: 0.5s;
}

.prodItem:hover .catalogImg {
    transition-duration: 0.5s;
    transform: scale(1.2);
}